import React, {useEffect, useMemo, useRef } from "react";
import '../../../css/admin/adminCs.css';
import {Col, Container, Row,Accordion } from "react-bootstrap";
import { useState } from "react";
import AdminAsideMenu from "../AdminAsideMenu";
import {Link, useNavigate} from "react-router-dom";
import ReactQuill from "react-quill";
import 'react-quill/dist/quill.snow.css';
import axios from "axios";

function Write() {
    const navigate = useNavigate();
    // 전송할 데이터
    const [content, setContent] = useState("");
    const [title, setTitle] = useState("");
    const [registant, setRegistant] = useState('');

    const quillRef = useRef(null);
    const { getEditor } = quillRef.current;


    // 제목 내용 수집
    const handleTitleChange = (e) => {
        setTitle(e.target.value);
    };

    // 로그인 된 이메일 가져오기
    useEffect(()=>{
        const email = localStorage.getItem('email');
        if (email) {
            setRegistant(email.slice(0, 3) + '***');
        }
    },[]);

    const handleImage = () => {
        const input = document.createElement("input");
        input.setAttribute("type", "file");
        input.setAttribute("accept", "image/*");
        input.click();
        input.onchange = async () => {
            const file = input.files[0];

            // 현재 커서 위치 저장
            const range = getEditor().getSelection(true);

            // 서버에 올려질때까지 표시할 로딩 placeholder 삽입
            getEditor().insertEmbed(range.index, "image", `/images/loading.gif`);

            try {
                // 필자는 파이어 스토어에 저장하기 때문에 이런식으로 유틸함수를 따로 만들어줬다
                // 이런식으로 서버에 업로드 한뒤 이미지 태그에 삽입할 url을 반환받도록 구현하면 된다
               /* const filePath = `contents/temp/${Date.now()}`;
                const url = await uploadImage(file, filePath);*/

                // 정상적으로 업로드 됐다면 로딩 placeholder 삭제
                getEditor().deleteText(range.index, 1);
                // 받아온 url을 이미지 태그에 삽입
                getEditor().insertEmbed(range.index, "image");

                // 사용자 편의를 위해 커서 이미지 오른쪽으로 이동
                getEditor().setSelection(range.index + 1);
            } catch (e) {
                getEditor().deleteText(range.index, 1);
            }
        };
    };

    // 에디터 설정
    const modules = {
        toolbar: {
            container: [
                [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
                [{ 'font': []}],
                [{ 'align': [] }],
                ['bold', 'italic', 'underline', 'strike', 'blockquote'],
                [{ 'list': 'ordered' }, { 'list': 'bullet' }, 'link'],
                [{ 'color': ['#000000', '#e60000', '#ff9900', '#ffff00', '#008a00', '#0066cc', '#9933ff', '#ffffff', '#facccc', '#ffebcc', '#ffffcc', '#cce8cc', '#cce0f5', '#ebd6ff', '#bbbbbb', '#f06666', '#ffc266', '#ffff66', '#66b966', '#66a3e0', '#c285ff', '#888888', '#a10000', '#b26b00', '#b2b200', '#006100', '#0047b2', '#6b24b2', '#444444', '#5c0000', '#663d00', '#666600', '#003700', '#002966', '#3d1466', 'custom-color'] }, { 'background': [] }],
                ['image', 'video'],
                ['clean'],
            ],
        },
    };


    const formats = [
        'header',
        'bold', 'italic', 'underline', 'strike', 'blockquote',
        'list', 'bullet',
        'link', 'image',
    ];

    return (
        <>
            <div>
                <form encType="multipart/form-data">
                    <h4>공지사항 제목 <em>(필수)</em></h4>
                    <input type="text" name="title" placeholder="제목을 작성해주세요." onChange={handleTitleChange}/>
                    <h4>내용 입력 <em>(필수)</em></h4>
                    <ReactQuill
                        style={{ width: "100%", height: "300px" }}
                        onChange={setContent}
                        modules={modules}
                        formats={formats}
                    />
                    <div className="links">
                        <Link to="/admin/notice/adminNoticeList" className="btnWrite noticeList">취소</Link>
                        <button type="button" className="btnWrite noticeReg" onClick={handleImage}>작성</button>
                    </div>
                </form>
            </div>
        </>
    );
}
function AdminNoticeWrite() {
    return (
        <section id="admin">
            <Container id="adminNoticeWrite">
                <Row className="justify-content-center">
                    {/* aside menu */}
                    <AdminAsideMenu/>
                    <Col className="write-content">
                        <div>
                            <h3>공지사항 작성</h3>
                        </div>
                        <div>
                            <Write/>
                        </div>
                    </Col>
                </Row>
            </Container>
        </section>
    );
}

export default AdminNoticeWrite;